import { useForm } from 'react-hook-form';
import React from 'react';
import { Button } from '../../../components';
import clsx from 'clsx';

//    
type TAuth = 'reg' | 'auth';

type TAuthFormData = {
  login: string;
  pass: string;
};

interface IAuthForm {
  /**   */
  authType: TAuth;
}

/**
 *   
 *
 *    :   
 *
 *    , , ,   
 *     
 *
 * @param authType  
 *
 * @returns React.FC
 */

export const AuthForm: React.FC<IAuthForm> = ({ authType }) => {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm<TAuthFormData>();

  const [authTypeInner, setAuthTypeInner] = React.useState<TAuth>(authType);

  const isRegProcedure = authTypeInner === 'reg';

  const onSubmit = (data: TAuthFormData) => {
    console.log(`    ${isRegProcedure ? '' : ''}: `, data);
    reset();
  };

  //       authType
  React.useEffect(() => {
    reset();
    if (authType === 'auth') {
      setAuthTypeInner('auth');
    } else setAuthTypeInner('reg');
  }, [authType, reset]);

  return (
    <div className="flex-column authorize-container">
      <h3 className="margin-bottom-8">{isRegProcedure ? '' : ''}</h3>
      <form className="box form authorize" onSubmit={handleSubmit(onSubmit)}>
        <label htmlFor="login"></label>
        <div className="grid-content">
          <input
            {...register('login', {
              required: '  "" ',
              pattern: {
                value: /\S+@\S+\.\S+/,
                message: '    ',
              },
            })}
            className={clsx(errors.login && 'error-field')}
            type="text"
            id="login"
            placeholder=" "
          />
          {errors.login && <p className="error">{errors.login.message}</p>}
        </div>

        <label htmlFor="pass"></label>
        <div className="grid-content">
          <input
            {...register('pass', {
              required: '  "" ',
              minLength: isRegProcedure && {
                value: 8,
                message: '    8 ',
              },
            })}
            className={clsx(errors.login && 'error-field')}
            type="password"
            id="pass"
            placeholder=" "
          />
          {errors.pass && <p className="error">{errors.pass.message}</p>}
        </div>

        <div className="grid-content flex-row margin-top-12">
          <Button className="primary small" type="submit">
            {isRegProcedure ? '' : ''}
          </Button>
          {!isRegProcedure && (
            <Button
              className="margin-left-12 small"
              type="submit"
              onClick={() => {
                setAuthTypeInner('reg');
                reset();
              }}
            >
              
            </Button>
          )}
          {isRegProcedure && (
            <Button
              className="margin-left-12 small"
              type="submit"
              onClick={() => {
                setAuthTypeInner('auth');
                reset();
              }}
            >
                
            </Button>
          )}
        </div>
      </form>
    </div>
  );
};
