import React from 'react';
import { Button, Layout } from '../components';
import { ProfileForm } from '../features/forms';

export type ProfileT = {
  name: string;
  aboutMe: string;
};

export const Profile: React.FC = () => {
  const [isEditMode, setIsEditMode] = React.useState<boolean>(false);
  const [form, setForm] = React.useState<ProfileT>({
    name: ' ',
    aboutMe:
      '  ,   .        ,        .',
  });

  const sendHandler = (): void => {
    setIsEditMode(false);
  };

  return (
    <Layout>
      <h1 className="margin-bottom-32"></h1>
      {!isEditMode ? (
        <>
          <dl className="list">
            <dt></dt>
            <dd>{form.name}</dd>
            <dt> </dt>
            <dd>{form.aboutMe}</dd>
          </dl>
          <div className="footer txt-right">
            <Button onClick={() => setIsEditMode((prev) => !prev)}></Button>
          </div>
        </>
      ) : (
        <ProfileForm
          className="margin-top-32 margin-bottom-32"
          form={form}
          setForm={setForm}
          sendHandler={sendHandler}
        />
      )}
    </Layout>
  );
};
