import { useForm } from 'react-hook-form';
import React from 'react';
import { Button } from '../../../components';
import clsx from 'clsx';

//    
type TProcedure = 'add' | 'edit';

type TAuthFormData = {
  title: string;
  category: string;
  imgPath: string[];
  describe: string;
  price: number;
};

interface IProductForm {
  /**   */
  procedureType: TProcedure;
}

/**
 *  / 
 *
 *    
 *
 * @param procedureType  
 *
 * @returns React.FC
 */

export const ProductForm: React.FC<IProductForm> = ({ procedureType }) => {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors },
  } = useForm<TAuthFormData>();

  //     " "
  const isAddProcedure = procedureType === 'add';

  //       
  const defaultFieldValue = '-   ';

  const onSubmit = (data: TAuthFormData) => {
    console.log(`    ${isAddProcedure ? ' ' : ' '}: `, data);
    reset();
  };

  //       procedureType
  React.useEffect(() => {
    reset();
  }, [procedureType, reset]);

  return (
    <form className="margin-top-24 form" onSubmit={handleSubmit(onSubmit)}>
      <label htmlFor="login"></label>
      <input
        {...register('title', {
          value: !isAddProcedure ? defaultFieldValue : null,
          required: true,
        })}
        className={clsx(errors.title && 'error-field', 'grid-content')}
        type="text"
        id="title"
        placeholder=" "
      />

      <label htmlFor="category"></label>
      <select
        {...register('category', {
          value: !isAddProcedure ? 't-shirts' : null,
          required: true,
        })}
        className={clsx(errors.category && 'error-field', 'grid-content')}
        id="category"
      >
        <option value=""> </option>
        <option value="t-shirts">, </option>
        <option value="clothes"> </option>
        <option value="boots"></option>
      </select>

      <label htmlFor="imgPath">  </label>
      <textarea
        {...register('imgPath', {
          value: !isAddProcedure ? [defaultFieldValue] : undefined,
          required: true,
        })}
        className={clsx(errors.title && 'error-field', 'grid-content')}
        id="imgPath"
        placeholder="   "
      />

      <label htmlFor="login"></label>
      <textarea
        {...register('describe', {
          value: !isAddProcedure ? defaultFieldValue : undefined,
          required: true,
        })}
        className={clsx(errors.describe && 'error-field', 'grid-content')}
        id="describe"
        placeholder=" "
      />

      <label htmlFor="login"></label>
      <input
        {...register('price', {
          value: !isAddProcedure ? 10000 : undefined,
          required: true,
        })}
        className={clsx(errors.price && 'error-field', 'grid-content')}
        type="number"
        id="price"
        placeholder=" "
      />

      <Button className="small margin-top-8" type="submit">
        {isAddProcedure ? '' : ''}
      </Button>
    </form>
  );
};
